<template>
  <div class="link-edit">
    <section class="section">
      <div class="container">
        <div class="columns is-multiline">
          <div class="column is-8">
            <h1 class="has-text-primary is-size-3">
              {{ link.name }}
              <span :class="`tag ${tagType(link.status)} is-size-6`">{{ linkStatus(link.status) }}</span>
            </h1>
            <hr>
            <div class="tags">
              <span class="mr-2">Identy: </span> <span class="tag is-primary">{{ link.identy }}</span>
            </div>
            <b-field label="Url">
              <b-input
                placeholder="Disabled"
                :disabled="urlChange"
                v-model="form.url"></b-input>
            </b-field>
            <div class="">
              <a href="#" @click="urlChangeActive" v-if="urlChange">Change</a>
              <a href="#" @click="urlChangeSave(link.url, form.url)" class="has-text-success mr-2" v-if="!urlChange">Save</a>
              <a href="#" @click="urlChangeCancel(link.url)" class="has-text-danger ml-2" v-if="!urlChange">Cancel</a>
            </div>

          </div>
          <div class="column is-4">
            <b-button
              type="is-success"
              label="Update"
              class="mr-2"
              @click.prevent="updateLink"
              :disabled="buttonActive"
              ></b-button>
            <b-button
              type="is-danger"
              class="ml-2"
              label="Cancel"
              @click="cancelEdit"
              ></b-button>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Link from '@/api/Link'

export default {
  name: 'LinkEdit',
  data () {
    return {
      link: null,
      urlChange: true,
      buttonActive: true,
      form: {
        url: null
      }
    }
  },
  methods: {
    getLink: function () {
      Link.getLinkEdit(this.$route.params.id).then((response) => {
        console.log(response)
        if(response.status === 200){
          this.link = response.data
          this.form.url = response.data.url
        }
      }).catch((errors) => {
        console.log(errors)
      })
    },
    updateLink: function () {
      Link.updateLink(this.$route.params.id, this.form).then((response) => {
        if(response.status === 200 && response.data.message === 'Ok'){
          this.$buefy.notification.open({
            type: 'is-success',
            message: 'Link update'
          })
        }
      }).catch((errors) => {
        console.log(errors)
      })
    },
    cancelEdit: function () {
      this.$router.back()
    },

    tagType: function (status) {
      if(status === 'on'){
        return 'is-success'
      }else if(status === 'report'){
        return 'is-warning'
      }else if(status === 'of'){
        return 'is-danger'
      }

    },

    linkStatus: function (status){
      if(status === 'on'){
        return 'Clean'
      }else if(status === 'report'){
        return 'Reported'
      }else if(status === 'of'){
        return 'Broked'
      }
    },

    urlChangeActive: function () {
      this.urlChange = !this.urlChange
      this.buttonActive = !this.buttonActive
    },

    urlChangeSave: function (url, urlChange) {
      // this.urlChange = !this.urlChange
      if(url === urlChange){
        this.buttonActive = !this.buttonActive
      }
    },

    urlChangeCancel: function (url) {
      this.form.url = url;
      this.urlChange = !this.urlChange
    }

  },
  mounted () {
    this.getLink()
  }
}
</script>
